import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import { NavigationContainer } from '@react-navigation/native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import React from 'react';
import BackButton from '../../components/BackButton';
import FeedbackOverflowMenu from '../../components/FeedbackOverflowMenu';
import LazyLoader from '../../components/LazyLoader';
import { SCREEN_OPTIONS } from '../../constants';
import { navigationRef } from '../../util/navigate';
import EditorBackButton from '../Editor/EditorBackButton';
import EditorOverflowMenu from '../Editor/EditorOverflowMenu';
import Record from '../Record/Record';
import NavigationBar from './NavigationBar';

const Stack = createNativeStackNavigator();
const Tab = createBottomTabNavigator();

const makeLazyScreen = (importPromise) => {
  return () => <LazyLoader LazyComponent={React.lazy(() => importPromise)} />;
};

const TabNavigator = () => {
  return (
    <Tab.Navigator tabBar={NavigationBar}>
      <Tab.Screen
        name='Record'
        component={Record}
        options={{
          ...SCREEN_OPTIONS,
          title: 'Beatbox',
          headerRight: FeedbackOverflowMenu,
        }}
      />
      <Tab.Screen
        name='Library'
        component={makeLazyScreen(import('../Library/Library'))}
        options={{
          ...SCREEN_OPTIONS,
          headerRight: FeedbackOverflowMenu,
        }}
      />
      <Tab.Screen
        name='Settings'
        component={makeLazyScreen(import('../Settings/Settings'))}
        options={{
          ...SCREEN_OPTIONS,
        }}
      />
    </Tab.Navigator>
  );
};

const Navigator = () => {
  return (
    <NavigationContainer ref={navigationRef}>
      <Stack.Navigator>
        <Stack.Screen
          name='Tab Navigator'
          component={TabNavigator}
          options={{ headerShown: false }}
        />
        <Stack.Screen
          name='Sign up'
          component={makeLazyScreen(import('../SignIn/SignUp'))}
          options={{
            ...SCREEN_OPTIONS,
            headerLeft: BackButton,
          }}
        />
        <Stack.Screen
          name='Log in'
          component={makeLazyScreen(import('../SignIn/LogIn'))}
          options={{
            ...SCREEN_OPTIONS,
            headerLeft: BackButton,
          }}
        />
        <Stack.Screen
          name='Editor'
          component={makeLazyScreen(import('../Editor/Editor'))}
          options={{
            ...SCREEN_OPTIONS,
            headerLeft: EditorBackButton,
            headerRight: EditorOverflowMenu,
          }}
        />
      </Stack.Navigator>
    </NavigationContainer>
  );
};

export default Navigator;