import { Suspense } from 'react';
import styled from 'styled-components/native';
import { COLORS, FONT_BOLD, LARGE_FONT_SIZE, STANDARD_MARGIN } from '../constants';

const Container = styled.View`
  flex: 1;
  background-color: ${COLORS.BACKGROUND};
  padding: ${STANDARD_MARGIN}px;
`;

const LoadingText = styled.Text`
  font-size: ${LARGE_FONT_SIZE}px;
  color: ${COLORS.DARK};
  font-family: ${FONT_BOLD};
`;

const LazyLoader = ({ LazyComponent }) => {
  return (
    <Suspense fallback={
      <Container>
        <LoadingText>
          Loading...
        </LoadingText>
      </Container>
    }>
      <LazyComponent />
    </Suspense>
  );
};

export default LazyLoader;